"use client"
import React from "react";
import Image from "next/image";
import { useState, useEffect } from "react";

const Company = (props) => {
  const [left, setLeft] = useState(true);

  useEffect(() => {
    if(props.number % 2 === 0) {
      setLeft(false);
    }
  }, []);
  return (
    <div className={"bg-sky-300 text-black rounded-lg p-4 w-1/2"} id="company">
      <h1 className="text-4xl mb-4">{props.companyName}</h1>
      <div className="flex">
        <div className="w-56 bg-black rounded-3xl">
          <Image src={props.companyLogo} alt="Company logo" height={300} width={300} className=""/>
        </div>
        <span className="ml-2 mt-4">
          <div className="text-xl mb-3">{props.companyDescription}</div>
          <div className="text-xl">{"\""+props.companyFeedback+"\""}</div>
        </span>
      </div>
    </div>
  );
}

export default Company;